:root {
  --color-comments: #a0a1a7;
  --color-function: #429ef5;
  --color-f-name: #f81143;
  --color-string: rgb(80, 165, 77);
  --color-method: rgb(228, 137, 0);
  --color-apex: rgb(106, 0, 228);
  --color-main: linear-gradient(-45deg, #c3d1e4 0%, #dde7f3 53.92%, #d4e0ed 100%);
  --color-cards: linear-gradient(to bottom, #f1f5f8 0%, #ebf3fa 0.01%, #dde7f3 53.92%, #e5f0f9 100%);
  --color-title: #31456a;
  --bg-color-element: #e3edf7;
  --box-inner-shadow: inset -3px -3px 7px #ffffff, inset 3px 3px 7px rgba(136, 165, 191, 0.58);
  --box-outer-shadow: -4px -2px 0.5rem #ffffff, 4px 2px 0.5rem rgba(136, 165, 191, 0.58);
  --box-inner-shadow-light: inset -3px -3px 7px #ffffff;
  --box-outer-shadow-light: -4px -2px 0.5rem #ffffff;
  --box-inner-shadow-dark: inset 3px 3px 7px rgba(136, 165, 191, 0.58);
  --box-outer-shadow-dark: 4px 2px 0.5rem rgba(136, 165, 191, 0.58);
  --shade: #d5dee7;
  --shade-2: #bfc7cf;
  --bg-input: #eaf5ff;
  --color-active: #f0f8ff;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: inherit;
  font-family: "Ubuntu", sans-serif;
  text-shadow: 0 1px 1px rgba(26, 26, 26, 0.1);
  transition: all 0.3s ease;
}

html {
  color: #1a1a1a;
  font-weight: normal;
  font-size: 16px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  color: var(--color-title);
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background: var(--bg-color-element);
}
.page::-webkit-scrollbar {
  display: none;
}

p {
  margin-bottom: 0;
  font-size: 1.125rem;
}

img {
  image-rendering: pixelated;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a,
button,
input {
  color: inherit;
  font: inherit;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
}

::selection {
  text-shadow: none;
  background: #5a606836;
}

ul {
  list-style-type: none;
}

dl {
  margin-bottom: 1.25rem;
  font-weight: normal;
  font-size: 1.125rem;
  line-height: 1.5;
}
@media (max-width: 599px) {
  dl {
    margin-bottom: 1.25rem;
    font-size: 1rem;
  }
}

dt,
dd {
  color: inherit;
}

dt {
  float: left;
  margin-right: 0.3rem;
}

code {
  font-weight: 300;
  white-space: pre-wrap;
  text-shadow: 0 1px 1px rgba(26, 26, 26, 0.1);
  overflow-wrap: break-word;
}

abbr[title] {
  text-decoration: none;
  transition: all 0s ease;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  visibility: hidden;
  clip: rect 0 0 0 0;
}

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 81.25rem;
  padding: 0 0.95rem;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
.code__content-list + .code__text {
  margin-top: 1rem;
}

.text-regular {
  color: var(--color-title);
}

.result-color--apex {
  color: var(--color-apex);
}

.result-color--comments {
  color: var(--color-comments);
  font-style: italic;
}

.result-color--function {
  color: var(--color-function);
}

.result-color--function-name {
  color: var(--color-f-name);
}

.result-color--string {
  color: var(--color-string);
}

.result-color--method {
  color: var(--color-method);
}

.button {
  max-width: 15rem;
  margin: 0 auto;
  padding: 1rem 3rem;
  color: inherit;
  background: transparent;
  border-radius: 1.5rem;
  box-shadow: 0 0 0 #ffffff, 0.2rem 0.2rem 0.4rem rgba(136, 165, 191, 0.58);
  cursor: pointer;
}
.button:hover, .button:focus {
  outline: none;
  box-shadow: 0 0 0 #ffffff, 0.2rem 0.2rem 0.8rem rgba(136, 165, 191, 0.58);
}
.button--neumorph {
  box-shadow: inset 0 0 0 #ffffff, inset 0 0 0 rgba(136, 165, 191, 0.58), -5px -3px 0.5rem #ffffff, 5px 3px 0.5rem rgba(136, 165, 191, 0.58);
}
.button--neumorph:hover, .button--neumorph:focus {
  outline: none;
  box-shadow: inset -3px -3px 7px #ffffff, inset 3px 3px 7px rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.button--fizzbuzz {
  margin-top: 1rem;
  box-shadow: inset 0 0 0 #f4f8fc, inset 0 0 0 #f4f8fc, inset 0 0 0 #cfd9e3, inset 0 0 0 #cfd9e3, inset 0 0 0 2px #e3edf7, inset 0 0 0 0 #ffffff, inset 0 0 0 3px rgba(136, 165, 191, 0.281), -4px -2px 1rem white, 4px 2px 1rem rgba(136, 165, 191, 0.48);
}
.button--fizzbuzz:hover, .button--fizzbuzz:focus {
  background: #e3edf7;
  box-shadow: inset 1px 0 0 #f4f8fc, inset 0 1px 0 #f4f8fc, inset -1px 0 0 #cfd9e3, inset 0 -1px 0 #cfd9e3, inset 0 0 0 4px #e3edf7, inset -3px -3px 7px 4px #ffffff, inset 3px 3px 7px 4px rgba(136, 165, 191, 0.48), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.48);
}
.button--fizzbuzz:hover .code-method__button-text, .button--fizzbuzz:focus .code-method__button-text {
  color: rgba(49, 69, 106, 0.9);
  transform: scale(0.95);
}
@media (max-width: 599px) {
  .button--fizzbuzz {
    margin-top: 4rem;
  }
}
.button--move {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  --arrow-width: 2px;
  padding: 1rem 2rem;
  font-size: 2rem;
}
.button--move::before {
  content: attr(data-text);
}
@media (max-width: 599px) {
  .button--move {
    margin: 0;
  }
}

.code-method__button-text {
  display: inline-block;
  transition: all 0.3s ease;
}

.code-method__result-text {
  text-shadow: 0 1px 1px rgba(26, 26, 26, 0.1);
}

.header {
  position: sticky;
  top: 0;
  z-index: 3;
  background: var(--bg-color-element);
}
.header.scroll {
  box-shadow: 0 2px 4px rgba(136, 165, 191, 0.58);
}

.brand {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 1.875rem;
  letter-spacing: -0.03em;
}
@media (max-width: 599px) {
  .brand {
    margin-right: 0;
  }
}

.brand__name {
  margin-left: 0.675rem;
}
@media (max-width: 980px) {
  .brand__name {
    display: none;
  }
}

.brand__icon {
  display: flex;
}
.brand__icon .icon__logo {
  fill: var(--color-title);
}

.menu {
  display: grid;
  grid-template-rows: max-content;
  grid-template-columns: max-content 1fr minmax(max-content, 12rem);
  gap: 2.5rem;
  padding: 0.625rem 1.25rem;
}
@media (max-width: 768px) {
  .menu {
    position: relative;
    grid-template-columns: max-content minmax(100px, 1fr) minmax(max-content, 46px);
    gap: 2rem;
  }
}
@media (min-width: 980px) {
  .menu {
    grid-template-rows: max-content;
    grid-template-columns: max-content minmax(max-content, 1fr) minmax(max-content, 12rem);
    gap: 2.5rem;
  }
}
.menu__item {
  height: 100%;
}
.menu__item:nth-child(1) {
  flex-grow: 0;
}
.menu__item:nth-child(2) {
  flex-grow: 3;
}
.menu p {
  margin-bottom: 0;
}

.search__mobile {
  display: none;
}
@media (max-width: 599px) {
  .search__mobile {
    display: flex;
    height: 100%;
  }
}

.search__button {
  display: none;
}
@media (max-width: 599px) {
  .search__button {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    outline: none;
    fill: #7f7f7f;
  }
  .search__button:focus {
    box-shadow: 0 3px 6px #e8e8e8, 0 1px 3px #7f7f7f;
  }
  .search__button--search {
    width: 100%;
    max-width: 46px;
  }
  .search__button--close:focus {
    box-shadow: 0 3px 6px #e8e8e8, 0 1px 3px #7f7f7f;
  }
}

.search__wrapper {
  height: 100%;
}
@media (max-width: 599px) {
  .search__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: grid;
    grid-template-columns: 1fr 46px;
    gap: 20px;
    width: 100%;
    padding: 0.625rem 1.25rem;
    background: var(--bg-color-element);
    clip-path: circle(0 at calc(2rem + 90px) 33px);
  }
}
.search__wrapper.active {
  z-index: 100;
  clip-path: circle(100% at calc(2rem + 90px) 33px);
}

.menu__label {
  position: relative;
  display: block;
  height: 100%;
}
@media (max-width: 599px) {
  .menu__label {
    display: flex;
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.icon__logo {
  width: 100%;
  max-width: 46px;
  height: 100%;
  max-height: 46px;
}

.search__icon {
  position: absolute;
  top: 50%;
  left: 1.75rem;
  display: flex;
  align-items: center;
  max-width: 25px;
  transform: translateY(-50%);
  fill: #7f7f7f;
}
@media (max-width: 599px) {
  .search__icon {
    justify-content: center;
    border-radius: 50%;
  }
}

.icon__search {
  max-width: 29px;
  max-height: 21px;
}

.menu__input {
  display: block;
  width: 100%;
  height: 100%;
  min-height: 2.5rem;
  padding: 0.625rem 0.75rem 0.625rem 4rem;
  color: var(--color-title);
  font-size: 1rem;
  line-height: 1.4;
  background: var(--bg-input);
  border-radius: 3rem;
  box-shadow: inset -1px -1px 3px #ffffff, inset 1px 1px 3px rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.menu__input:focus {
  background: var(--color-active);
  outline: none;
  box-shadow: inset -1px -1px 3px #ffffff, inset 1px 1px 3px rgba(136, 165, 191, 0.911), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}

.counter-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
}

.counter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-weight: 700;
  font-size: 0.875rem;
  letter-spacing: -0.03em;
  background: var(--bg-input);
  border-radius: 2.5rem;
  box-shadow: inset -1px -1px 3px #ffffff, inset 1px 1px 3px rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.counter:hover, .counter:focus {
  background: var(--color-active);
  box-shadow: inset -2px -2px 5px #ffffff, inset 2px 2px 5px rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.counter:hover .counter__total, .counter:focus .counter__total {
  color: inherit;
  transform: scale(0.95);
}
@media (max-width: 599px) {
  .counter {
    font-size: 1rem;
  }
}

.page__title {
  margin-top: -0.5rem;
  margin-bottom: 3rem;
  margin-left: -0.187rem;
  font-weight: 700;
  font-size: 2.875rem;
  line-height: 1;
  letter-spacing: -0.03em;
}
@media (max-width: 599px) {
  .page__title {
    font-size: 1.875rem;
  }
}

.hero__text {
  margin-bottom: 1.75rem;
  line-height: 1.58;
}
@media (max-width: 599px) {
  .hero__text {
    margin-bottom: 1.25rem;
    font-size: 1rem;
  }
}

.hero__description {
  padding: 3.75rem 1.25rem 0;
}

.challenges {
  margin-bottom: 3.75rem;
}

.challenges__description {
  padding: 0.75rem 1.25rem 3.75rem;
}

.challenges__container {
  display: grid;
  grid-auto-flow: dense;
  grid-template-rows: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  gap: 1.25rem;
}

.filter {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20px, max-content));
  gap: 1.25rem;
}
@media screen and (max-width: 22.5rem) {
  .filter {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

@media (max-width: 599px) {
  .filter__item {
    flex-grow: 1;
  }
}

.filter__button {
  color: rgba(49, 69, 106, 0.5);
  font-weight: 700;
  font-size: 1.125rem;
  outline: none;
}
.filter__button:hover, .filter__button:focus {
  color: #68778a;
}
.filter__button.active {
  position: relative;
  color: inherit;
}
.filter__button.active::after {
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  display: block;
  width: 0%;
  height: 2px;
  background: var(--color-title);
  content: "";
}
.filter__button.active:hover::after, .filter__button.active:focus::after {
  width: 100%;
}

.challenges__link {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  font-weight: 500;
  background: linear-gradient(-45deg, #ebf0f5 50%, #f5f8fa 50%);
  background-position: 100% 100%;
  background-size: 200% 200%;
  border-radius: 1.5rem;
  box-shadow: inset 0 0 0 #ffffff, inset 0 0 0 rgba(136, 165, 191, 0.58), -4px -2px 0.5rem #ffffff, 4px 2px 0.5rem rgba(136, 165, 191, 0.58);
}
.challenges__link:hover, .challenges__link:focus {
  background-position: 0% 100%;
  box-shadow: inset -4px -2px 0.5rem #ffffff, inset 4px 2px 0.5rem rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.challenge--interview .challenges__link {
  color: white;
  background: linear-gradient(-45deg, #3f9ce9 50%, #42a5f5 50%);
  background-position: 100% 100%;
  background-size: 200% 200%;
}
.challenge--interview .challenges__link:hover, .challenge--interview .challenges__link:focus {
  background-position: 0% 100%;
  box-shadow: inset 0 0 0 #ffffff60, inset 0 0 0 rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.challenge--interview .challenges__link .tag__badge {
  color: white;
}
.challenge--inspiration .challenges__link {
  color: white;
  background: linear-gradient(-45deg, #e63067 50%, #f33363 50%);
  background-position: 100% 100%;
  background-size: 200% 200%;
}
.challenge--inspiration .challenges__link:hover, .challenge--inspiration .challenges__link:focus {
  background-position: 0% 100%;
  box-shadow: inset 0 0 0 #ffffff60, inset 0 0 0 rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.challenge--inspiration .challenges__link .tag__badge {
  color: white;
}
.challenge--course .challenges__link {
  color: white;
  background: linear-gradient(-45deg, #8c60b1 50%, #9266bb 50%);
  background-position: 100% 100%;
  background-size: 200% 200%;
}
.challenge--course .challenges__link:hover, .challenge--course .challenges__link:focus {
  background-position: 0% 100%;
  box-shadow: inset 0 0 0 #ffffff60, inset 0 0 0 rgba(136, 165, 191, 0.58), 0 0 0 #ffffff, 0 0 0 rgba(136, 165, 191, 0.58);
}
.challenge--course .challenges__link .tag__badge {
  color: white;
}

.challenge {
  display: block;
  transition: all 0.6s ease;
}
.challenge--hidden {
  display: none;
  transition: all 0.6s ease;
}

.challenge__text {
  font-size: 1rem;
  text-shadow: 0 1px 1px rgba(26, 26, 26, 0.1);
}

.challenge__inner {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 290px;
  padding: 1.25rem;
}

.challenge__title {
  flex: 1;
  height: 100%;
  margin-bottom: 0.625rem;
  font-weight: 700;
  text-shadow: 0 1px 1px rgba(26, 26, 26, 0.1);
}
@media (max-width: 599px) {
  .challenge__title {
    font-size: 1.875rem;
  }
}

.challenge__tag-list {
  margin-bottom: 0.625rem;
}

.author {
