  }
  50% {
    background-position: 0% 60%;
  }
  75% {
    background-position: 100% 40%;
  }
  100% {
    background-position: 100% 85%;
  }
}
@keyframes sbHoverOut {
  0% {
    background-position: 0% 85%;
  }
  50% {
    background-position: 100% 40%;
  }
  75% {
    background-position: 0% 60%;
  }
  100% {
    background-position: 100% 0%;
  }
}

